.metaslider,
#container {
  width: 100%;
  top: 0;
  margin-left: 0;
  z-index: 3000;
  position: relative;
  margin-bottom: 10px;
}

.metaslider:before, .metaslider:after,
#container:before,
#container:after {
  z-index: -1;
  position: absolute;
  content: "";
  bottom: 15px;
  left: 10px;
  width: 50%;
  top: 80%;
  max-width: 300px;
  background: #777;
  box-shadow: 0 15px 10px #777;
  transform: rotate(-3deg);
}

.metaslider:after,
#container:after {
  transform: rotate(3deg);
  right: 10px;
  left: auto;
}

#slides {
  display: none;
}

/* Center the slideshow */
#container {
  width: 400px;
  height: 266px;
  z-index: 1;
}

#container:before, #container:after {
  animation: 1s 5s fadeIn;
  visibility: hidden;
  animation-fill-mode: forwards;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
